// Utils
async function delay(ms: number) { return new Promise(res => setTimeout(res, ms)); }

async function callWithRetry<T>(fn: () => Promise<T>, retries = 2, baseDelayMs = 5000): Promise<T> {
  let attempt = 0;
  let lastErr: any;
  while (attempt <= retries) {
    try { return await fn(); } catch (err: any) {
      lastErr = err;
      const msg = String(err?.message || err);
      const isQuota = msg.includes('429') || msg.toLowerCase().includes('quota');
      const isTransient = isQuota || msg.toLowerCase().includes('timeout');
      if (attempt === retries || !isTransient) break;
      const jitter = Math.floor(Math.random() * 1000);
      const delayMs = baseDelayMs * Math.pow(2, attempt) + jitter;
      try { fs.appendFileSync(path.join(process.cwd(),'exports','uploadMenu-debug.log'), `[${new Date().toISOString()}] retrying AI in ${delayMs}ms (attempt ${attempt+1}/${retries}) msg=${msg}\n`, 'utf8'); } catch {}
      await delay(delayMs);
      attempt++;
    }
  }
  throw lastErr;
}

function ensureExportsDir() {
  const exportDir = path.join(process.cwd(), 'exports');
  if (!fs.existsSync(exportDir)) fs.mkdirSync(exportDir, { recursive: true });
  return exportDir;
}

function exportToCSV(items: any[]) {
  const header = ['id', 'name', 'category', 'price', 'ingredients', 'image', 'extractionMethod'];
  const escape = (v: any) => '"' + String(v ?? '').replace(/"/g, '""') + '"';
  const csvRows = [header.join(',')];
  for (const it of items) {
    const row = [it.id, it.name, it.category, it.price, Array.isArray(it.ingredients) ? it.ingredients.join('; ') : it.ingredients, it.image || '', it.extractionMethod || ''];
    csvRows.push(row.map(escape).join(','));
  }
  return csvRows.join('\n');
}

async function saveCSVFile(content: string, filename: string) {
  const dir = ensureExportsDir();
  const full = path.join(dir, filename);
  await fs.promises.writeFile(full, content, 'utf8');
  return full;
}

async function processBatch<T, R>(items: T[], batchSize: number, processor: (chunk: T[]) => Promise<R[]>, delayMs = 500) {
  const results: R[] = [];
  for (let i = 0; i < items.length; i += batchSize) {
    const chunk = items.slice(i, i + batchSize);
    const res = await processor(chunk);
    if (Array.isArray(res)) results.push(...res as any);
    await delay(delayMs);
  }
  return results;
}

function getFallbackIngredients(name: string, category?: string) {
  const lower = (name || '').toLowerCase();
  if (lower.includes('chicken')) return ['chicken', 'salt', 'pepper'];
  if (lower.includes('rice') || lower.includes('biryani')) return ['rice', 'onion', 'spices'];
  if (lower.includes('dal') || lower.includes('lentil')) return ['lentils', 'turmeric', 'salt'];
  if (lower.includes('soup')) return ['water', 'salt', 'vegetables'];
  if (category && category.toLowerCase().includes('dessert')) return ['sugar', 'milk'];
  return ['water', 'salt'];
}

function calculateAccuracy(items: any[]) {
  if (!Array.isArray(items) || items.length === 0) return 0;
  const score = items.reduce((acc, it) => acc + (Array.isArray(it.ingredients) && it.ingredients.length > 0 ? 1 : 0), 0);
  return Math.round((score / items.length) * 100);
}

function extractHeuristicFromText(text: string) {
  const lines = text.split(/\r?\n/).map((s: string) => s.trim()).filter(Boolean);
  const priceRe = /(\d+[\.,]\d{1,2})(\s*(‚Ç¨|EUR|USD|\$|GBP))?/;
  const extractedItems: any[] = [];
  for (const line of lines) {
    const m = line.match(priceRe);
    if (m) {
      const price = m[0].trim();
      let name = line.replace(priceRe, '').replace(/[\.]{2,}.*$/, '').replace(/[-‚Äì‚Äî]+\s*$/, '').trim();
      if (name && name.length > 1) {
        extractedItems.push({ id: `menu-${Date.now()}-${extractedItems.length}`, name, price, category: 'Uncategorized', image: '', ingredients: [], extractionMethod: 'text-heuristic' });
      }
    }
  }
  return extractedItems;
}

